import React from 'react';
import logo from '../../images/logo.png';
import './header.css'

const header = () => {
    return (
        <div className="header">
            <div className="logo">
                <img src={logo} alt="" />
             </div>
             <nav>
                 <a href="/shop">Home</a>
                 <a href="/feature">Feature</a>
                 <a href="inventory">Inventory</a>
                 <a href="order">Order</a>
             </nav>
        </div>
    );
};

export default header;