import React, { useEffect, useState } from 'react';
import { addToDb, getStoredCart } from '../../utilities/fakedb';
import Cart from '../cart/cart';
import Product from '../product/product';
import './shop.css'

const Shop = () => {
    const [products,setProduct]=useState([])
    const [cart,setCart] = useState([])
    const [searchProductDisplay,setSearchProductDisplay] = useState([])
    useEffect(()=>{
        fetch('./products.JSON')
        .then(res => res.json())
        .then(data => {
            setProduct(data)
            setSearchProductDisplay(data)
        })
    },[])

    useEffect(()=>{
        const saveCart = getStoredCart()
        // console.log(saveCart)
        const storeCart = []
        if(products.length){
            for(const key in saveCart){
                console.log(key,saveCart[key])
                const addProduct = products.find(product => product.key === key)
                if(addProduct){
                    const quantity = saveCart[key]
                    addProduct.quantity = quantity;
                    storeCart.push(addProduct)
                }
               
            }
            setCart(storeCart)
        }
    },[products])

    const handleAddToCart = product => {
        const newCart = [...cart,product]
        setCart(newCart)
        addToDb(product.key)
    };
    const handleSearch = event =>{
        const searchProduct = event.target.value
        const matchedProduct = products.filter(product => product.name.toLowerCase().includes(searchProduct.toLowerCase()))
        setSearchProductDisplay(matchedProduct)
    }
    return (
        <div>
            <div className="search-container">
                <input onChange={handleSearch} type="text" placeholder="search Product"/>
            </div>
            <div>
                <div className="shop-container">
                    <div className="product-container">
                        {
                            // console.log(products)
                        }
                        <h2>Product:{products.length}</h2>
                        {
                            searchProductDisplay.map(product => <Product 
                                handleAddToCart={handleAddToCart} 
                                key={product.key} 
                                product={product}>
                                </Product>)
                        }
                    </div>
                    <div className="cart-container">
                        <Cart cart={cart}></Cart>
                    </div>
                </div>
           </div>
        </div>
    );
};

export default Shop;