import React from 'react';
const Cart = (props) => {
    const {cart}=props;
    let totalPrice = 0;
    let totalQuantity = 0;
    for(const product of cart){
        if(!product.quantity){
            product.quantity=1;
        }
       totalPrice = Math.round(totalPrice + (product.price*product.quantity))
       totalQuantity = totalQuantity + product.quantity;
    }
    const shipping = totalPrice > 0 ? 15 : 0;
    const tax = Math.round((totalPrice+shipping)*0.10);
    const grandTotal = Math.round(totalPrice+shipping+tax);
    
    return (
        <div>
            <h2>Product Summary: {totalQuantity}</h2>
            <h4>Price:{totalPrice}</h4>
            <h4>Shipping: {shipping}</h4>
            <h4>Tax: {tax}</h4>
            <h4>GrandTotal: {grandTotal}</h4>
        </div>
    );
};

export default Cart;